@charset 'utf-8';
@import '_utils';

//모바일 navi bar
.mNavi{
    display: none;
    height: 8rem; 
    z-index: 200; 

     //미디어쿼리
    @include desktop{
        display: block;
    }

    .fixed{
        position: fixed;
        align-items: center; 
        display: flex;
        width: 100%; 
        height: 8rem; 
        background: $main1; 
        color: $main2; 
        border-bottom: 0.1rem solid $shadow; 

        .left{
            text-align: center;
            z-index: 100;

            ul{
                display: flex; 
                margin-bottom: 0; 
                margin-left: 1rem;

                li{
                    display: flex; 
                    padding: 1rem; 
                    font-size: 1.8rem; 
                    cursor: pointer; 
                }
            }
        }

        .logo{
            position: absolute;
            width: 100%; 
            text-align: center;
            z-index: 50;
            
            img{
                cursor: pointer; 
                width: auto;
                height: 5rem;
            }
        }
    }
}

//모바일 햄버거 버튼
.mBtn{
    position: fixed; 
    top: 2rem; 
    right: 1rem; 
    display: none;
    margin-right: 1rem; 
    z-index: 1000; 

     //미디어쿼리
    @include desktop{
        display: block;
    }

    .menuBtn{
        display: flex; 
        flex-direction: column; 
        cursor: pointer; 
        padding: 2rem 1rem; 
        width: 3.5rem;

        span{
            display: block; 
            position: absolute; 
            top: calc(50% - 0.1rem); 
            left: calc(50% - 1.4rem); 
            width: 2.8rem; 
            height: 0.2rem; 
            border-radius: 0.2rem; 
            transition: 0.3s; 
            background: $main2;

            &:nth-child(1){
                transform: translate(0,-8px);
            }

            &:nth-child(3){
                transform: translate(0,8px);
            }
        }
    }

    //햄버거 버튼 addClass
    .active{
        span{
            &:nth-child(1){
                transform: translate(0,0) rotate(45deg) !important;
            }

            &:nth-child(2){
                transform:  scaleX(0) !important;
            }

            &:nth-child(3){
                transform: translate(0,0) rotate(-45deg) !important;
            }
        }
    }
}

//모바일 메뉴서랍
.mMenu{
    position: fixed;
    top: 0; 
    right: -30rem; 
    width: 30rem; 
    height: 100vh; 
    background: $main1;
    z-index: 900;
    overflow: hidden; 
    transition: 0.5s; 
    border-left: 0.1rem solid $shadow;
    text-align: left;

    ul{
        display: flex; 
        flex-direction: column; 
        margin-top: 10rem;
        
        li{
            padding: 1rem; 
            margin: 2rem; 
            color: #cdb376; 
            font-weight: bold; 
            font-style: italic; 
            font-size: 1.8em; 
            cursor: pointer;

            a{
                color: #cdb376; 
            }
        }
    }

    .mSubmenu{
        &>div{
            position: fixed; 
            top: -10rem; 
            right: -30rem; 
            

            .subMenu{
                width: 29.9rem; 
                height: 100vh; 
                background: $main1; 
                transition: 0.5s; 
                padding-top: 10rem;
    
                li{
                    font-style: normal !important;
                }
            }
        }
    }

    .subBottom{
        position: fixed; 
        width: 30rem; 
        bottom:1rem;

        ul{
            display: flex; 
            flex-direction: row; 
            align-items: center;
            
            li{
                font-size: 1.4rem;
                text-align: center;

                .icon{
                    font-size: 1.8rem; 
                    line-height: 0
                }
            }
            
        }
    }
}

.view{
    right: 0
}

.black{
    position: fixed; 
    top: 0;
    width: 100%;
    height: 100vh; 
    background: $black; 
    opacity: 0.8; 
    z-index: 800; 
}